* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
    font-family: Black Ops One;
}

html, body 
{
    height:100%;
}

.dark-mode {
    height: 100%;
    background: rgb(31, 30, 30);
    color: whitesmoke;
    transition: ease-in-out 0.3s;
    
}

.dark-mode .input {
    background: rgb(31, 30, 30);
    border: none;
    border-bottom: 1.5px solid white;
    color: rgb(52, 38, 175);
}

.togglecontainer {
    position: absolute;
    top: 50px;
    left: 20px;
}

.outputs {
    padding-left: 20px;
    height: 100%;
}

.dark-mode .outputs {
    background: rgb(31, 30, 30);
}

nav {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 20px;
    width: 100%;
    background: rgb(52, 38, 175);
}

ul {
    display: flex;
    justify-content: center;
    align-items: center;
    list-style: none;
}

li {
    padding: 10px;
    margin-right: 10px;
    font-size: 1.2rem;
    color: white;
    letter-spacing: 1px;
}

h1 {
    color: white;
}

h2 {
    font-size: 2rem;
    letter-spacing: 2px;
}

span {
    color:rgb(31, 30, 30);
    font-size: 2rem;
}

form {
    width: 100%;
    padding: 20px;
}

.input {
    width: 250px;
    height: 35px;
    outline: none;
    margin-top: 10px;
    margin-right: 15px;
    padding-left: 5px;
}


input[type="checkbox"] {
    position: relative;
    width: 70px;
    height: 20px;
    appearance: none;
    -webkit-appearance: none;
    background: grey;
    outline: none;
    border-radius: 20px;
    box-shadow: inset 0 0 5px rgba(0,0,0,.3);
    transition: .5s ease;
}

input[type="checkbox"]:checked {
    background: white;
}

input[type="checkbox"]:before {
    content: '';
    position: absolute;
    width: 20px;
    height: 20px;
    border-radius: 20px;
    top: 0;
    left: 0;
    background: rgb(31, 30, 30);
    transition: .5s;
    transform: scale(.95);
    box-shadow: 0 2px 5px rgba(0,0,0,.2);
}

input:checked[type="checkbox"]:before {
    left: 49px;
}

input:checked[type="checkbox"]:before .fa-adjust {
    color:rgb(31, 30, 30);
}


.btn {
    width: 75px;
    height: 35px;
    border: none;
    outline: none;
    margin-top: 15px;
    background: rgb(31, 30, 30);
    color: rgb(52, 38, 175);
    transition: 0.3s ease;
}

.dark-mode .btn {
    background: rgb(52, 38, 175);
    color: white;
}

.dark-mode .btn:hover {
    color: rgb(31, 30, 30);
}



.btn:hover {
    color: white;
    cursor: pointer;
}

::placeholder {
    color: rgb(52, 38, 175);
    font-size: 0.85rem;
}

table {
    zoom: 0.89;
}


th {
    margin-right: 20px;
    padding-right: 15px;
    font-size: 1.1rem;
    color: grey;
    letter-spacing: 1px;
    transition: ease-in 0.2s;
}

th:hover {
    cursor: pointer;
}

th:hover .fas {
    opacity: 1;
}

td {
    margin-right: 20px;
    padding-right: 15px;
    font-size: 1.1rem;
    font-family: Fugaz One;  
}

.displaynone {
    display: none;
}

.optionsummary {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: space-evenly;
    position: fixed;
    top: 0;
    height: 100vh;
    width: 500px;
    background: rgb(31, 30, 30);
    transform: translateX(-110%);
    clip-path: polygon(0 0, 4% 0, 25% 100%, 0% 100%);
    transition: ease 0.6s;
    opacity: 0;
}

.transIn {
    transform: translateX(0%);
    clip-path: polygon(0 0, 100% 0, 100% 100%, 0% 100%);
    opacity: 1;
}

.tradingsummary {
    transition: ease 0.3s;
    color: white;
}

.tradingsummary:hover {
    background: white;
    color: rgb(52, 38, 175);
    cursor: pointer;
}


.numtrades {
    padding-left: 75px;
    font-size: 1.2rem;
    font-family: Fugaz One;
}

p {
    border-top: 1px solid black;
    width: 500px;
    padding: 10px;
}

p:last-child {
    border-bottom: 1px solid black;
}

.delete {
    display: inline-block;
    font-size: 1.3rem;
    color: rgb(52, 38, 175);
    transition: 0.3s ease;
}

.delete:hover {
    transform: scale(1.3);
    cursor: pointer;
}

.table-responsive {
    min-height: .01%;
    overflow-x: auto;
}

.arrowup {
    transform: rotate(180deg);
}

.fas {
    font-size: 1rem;
    color: rgb(52, 38, 175);
    opacity: 0;
    transition: ease-in 0.3s;
}

.fa-adjust {
    opacity: 1;
    font-size: 20px;
    padding-left: 8px;
    color: white;
    transform: translateY(-3px);
}

@media screen and (max-width: 767px) {
    .outputs {
        width: 100%;
        overflow-y: hidden;
        -ms-overflow-style: -ms-autohiding-scrollbar;
        border: 1px solid #ddd;
    }
    .outputs > .table {
        margin-bottom: 0;
    }
    .outputs > .table > thead > tr > th,
    .outputs > .table > tbody > tr > th,
    .outputs > .table > tfoot > tr > th,
    .outputs > .table > thead > tr > td,
    .outputs > .table > tbody > tr > td,
    .outputs > .table > tfoot > tr > td {
        white-space: nowrap;
    }
}

